@import url('./colors.scss');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif, Arial, Helvetica;
    border: none;
}

html,
body {
    min-height: 100vh;
}

#root {
    min-height: 100vh;
    background-color: var(--body);
    color: var(--text);
}